import { createContext, useContext, useState, useEffect } from "react";
import axios from "axios";

const EmailContext = createContext();

export const EmailProvider = ({ children }) => {
  const [templates, setTemplates] = useState([]);
  const [campaigns, setCampaigns] = useState([]);
  const [loading, setLoading] = useState(false);

  const API_BASE = "http://localhost:5000";

  // Fetch templates
  const fetchTemplates = async () => {
    try {
      const response = await fetch(`${API_BASE}/emails/email-templates`, {
        credentials: "include",
      });
      const data = await response.json();
      if (data.success) {
        setTemplates(data.templates);
      }
    } catch (error) {
      console.error("Failed to fetch templates:", error);
    }
  };

  // Fetch campaigns
  const fetchCampaigns = async () => {
    try {
      const response = await fetch(
        `${API_BASE}/emails/email-logs?page=1&limit=50`,
        {
          credentials: "include",
        }
      );
      const data = await response.json();
      if (data.success) {
        setCampaigns(data.campaigns);
      }
    } catch (error) {
      console.error("Failed to fetch campaigns:", error);
    }
  };

  // Create template
  const createTemplate = async (formData) => {
    setLoading(true);
    try {
      console.log("Sending template data:", {
        name: formData.get("template_name"),
        attachments: formData.getAll("attachments").map((f) => f.name),
      });

      const response = await axios.post(
        `/emails/email-templates`, // Remove API_BASE as it's set in defaults
        formData,
        {
          headers: {
            "Content-Type": "multipart/form-data",
          },
          withCredentials: true,
          // Add timeout and other configs
          timeout: 30000,
          maxBodyLength: Infinity,
          maxContentLength: Infinity,
        }
      );

      if (response.data.success) {
        await fetchTemplates();
        return { success: true, message: response.data.message };
      }
      return { success: false, message: response.data.error };
    } catch (error) {
      console.error("Template creation error:", error);
      return {
        success: false,
        message: error.response?.data?.error || error.message,
      };
    } finally {
      setLoading(false);
    }
  };

  // Update template
  const updateTemplate = async (id, formData) => {
    setLoading(true);
    try {
      const response = await axios.put(
        `${API_BASE}/emails/email-templates/${id}`,
        formData,
        {
          headers: {
            "Content-Type": "multipart/form-data",
          },
          withCredentials: true,
        }
      );

      if (response.data.success) {
        await fetchTemplates();
        return { success: true, message: response.data.message };
      }
      return { success: false, message: response.data.error };
    } catch (error) {
      return {
        success: false,
        message: error.response?.data?.error || error.message,
      };
    } finally {
      setLoading(false);
    }
  };

  // Delete template
  const deleteTemplate = async (id) => {
    try {
      const response = await fetch(`${API_BASE}/emails/email-templates/${id}`, {
        method: "DELETE",
        credentials: "include",
      });
      const data = await response.json();
      if (data.success) {
        await fetchTemplates();
        return { success: true };
      }
      return { success: false };
    } catch (error) {
      return { success: false };
    }
  };

  // Send email campaign
  const sendCampaign = async (campaignData) => {
    setLoading(true);
    try {
      // Check if it's FormData (has manual attachments) or regular object
      const isFormData = campaignData instanceof FormData;

      const response = await fetch(`${API_BASE}/emails/email-logs/send`, {
        method: "POST",
        headers: isFormData ? {} : { "Content-Type": "application/json" },
        credentials: "include",
        body: isFormData ? campaignData : JSON.stringify(campaignData),
      });
      const data = await response.json();
      if (data.success) {
        await fetchCampaigns();
        return { success: true, data };
      }
      return { success: false, message: data.error };
    } catch (error) {
      return { success: false, message: error.message };
    } finally {
      setLoading(false);
    }
  };

  // Preview email
  const previewEmail = async (payload) => {
    try {
      const response = await fetch(`${API_BASE}/emails/email-logs/preview`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        credentials: "include",
        body: JSON.stringify(payload),
      });
      const data = await response.json();
      if (data.success) {
        return { success: true, preview: data.preview };
      }
      return { success: false, message: data.error };
    } catch (error) {
      return { success: false, message: error.message };
    }
  };

  // Delete campaign
  const deleteCampaign = async (id) => {
    try {
      const response = await fetch(`${API_BASE}/emails/email-logs/${id}`, {
        method: "DELETE",
        credentials: "include",
      });
      const data = await response.json();
      if (data.success) {
        await fetchCampaigns();
        return { success: true };
      }
      return { success: false };
    } catch (error) {
      return { success: false };
    }
  };

  useEffect(() => {
    fetchTemplates();
    fetchCampaigns();
  }, []);

  return (
    <EmailContext.Provider
      value={{
        templates,
        campaigns,
        loading,
        createTemplate,
        updateTemplate,
        deleteTemplate,
        sendCampaign,
        previewEmail,
        deleteCampaign,
        fetchTemplates,
        fetchCampaigns,
      }}
    >
      {children}
    </EmailContext.Provider>
  );
};

export const useEmail = () => {
  const context = useContext(EmailContext);
  if (!context) {
    throw new Error("useEmail must be used within EmailProvider");
  }
  return context;
};
