import { useState, useRef } from "react";
import { Send, Eye, X, FileText, Paperclip } from "lucide-react";
import { useEmail } from "../../context/EmailContext";

const ComposeEmail = () => {
  const { templates, sendCampaign, previewEmail, loading } = useEmail();
  const [previewData, setPreviewData] = useState(null);
  const [selectedTemplate, setSelectedTemplate] = useState(null);
  const [manualAttachments, setManualAttachments] = useState([]);
  const fileInputRef = useRef(null);
  const [formData, setFormData] = useState({
    title: "",
    subject: "",
    body: "",
    sender_email: "placement@college.edu",
    sendType: "filter",
    department: [],
    batch_year: "",
    min_cgpa: "",
    placement_status: "",
    recipient_emails: "",
    student_ids: "",
    event_id: "",
    recipient_type: "registered",
  });

  const handleUseTemplate = (template) => {
    setFormData({
      ...formData,
      title: template.template_name,
      subject: template.subject,
      body: template.body,
    });
    setSelectedTemplate(template);
  };

  const handlePreview = async () => {
    if (!formData.subject || !formData.body) {
      alert("Please enter subject and body");
      return;
    }

    const payload = {
      subject: formData.subject,
      body: formData.body,
    };

    // Add template_id if a template is selected
    if (selectedTemplate) {
      payload.template_id = selectedTemplate.id;
    }

    // const result = await previewEmail(payload);
    // if (result.success) {
    //   setPreviewData(result.preview);
    // }
    const result = await previewEmail(payload);
    if (result.success) {
      // Add manual attachments to preview
      const previewWithManual = {
        ...result.preview,
        attachments: [
          ...(result.preview.attachments || []),
          ...manualAttachments.map((file) => ({
            name: file.name,
            type: "manual",
            size: file.size,
          })),
        ],
      };
      setPreviewData(previewWithManual);
    }
  };

  const handleFileSelect = (e) => {
    const files = Array.from(e.target.files);

    // Validate file size (10MB max per file)
    const validFiles = files.filter((file) => {
      if (file.size > 10 * 1024 * 1024) {
        alert(`${file.name} is too large. Max size is 10MB`);
        return false;
      }
      return true;
    });

    // Check total file count (max 5 including template attachments)
    const templateAttachmentCount = getTemplateAttachments().length;
    const totalCount =
      manualAttachments.length + validFiles.length + templateAttachmentCount;

    if (totalCount > 5) {
      alert(`Maximum 5 attachments allowed (including template attachments)`);
      return;
    }

    setManualAttachments((prev) => [...prev, ...validFiles]);
  };

  const removeManualAttachment = (index) => {
    setManualAttachments((files) => files.filter((_, i) => i !== index));
  };

  const getTemplateAttachments = () => {
    if (!selectedTemplate?.attachments) return [];
    try {
      const attachments =
        typeof selectedTemplate.attachments === "string"
          ? JSON.parse(selectedTemplate.attachments)
          : selectedTemplate.attachments;
      return attachments;
    } catch (err) {
      return [];
    }
  };

  const getTotalAttachmentCount = () => {
    return getTemplateAttachments().length + manualAttachments.length;
  };

  // const handleSend = async () => {
  //   if (!formData.title || !formData.subject || !formData.body) {
  //     alert("Please fill title, subject, and body");
  //     return;
  //   }

  //   if (!confirm("Are you sure you want to send this email?")) return;

  //   const payload = {
  //     title: formData.title,
  //     subject: formData.subject,
  //     body: formData.body,
  //     sender_email: formData.sender_email,
  //   };

  //   // Add template_id if a template is selected (for attachments)
  //   if (selectedTemplate) {
  //     payload.template_id = selectedTemplate.id;
  //   }

  //   if (formData.sendType === "filter") {
  //     const filters = {};
  //     if (formData.department.length > 0)
  //       filters.department = formData.department;
  //     if (formData.batch_year)
  //       filters.batch_year = parseInt(formData.batch_year);
  //     if (formData.min_cgpa) filters.min_cgpa = parseFloat(formData.min_cgpa);
  //     if (formData.placement_status)
  //       filters.placement_status = formData.placement_status;
  //     payload.recipient_filter = filters;
  //   } else if (formData.sendType === "manual") {
  //     const emails = formData.recipient_emails
  //       .split(",")
  //       .map((e) => e.trim())
  //       .filter((e) => e);
  //     if (emails.length === 0) {
  //       alert("Please enter at least one email address");
  //       return;
  //     }
  //     payload.recipient_emails = emails;
  //   }

  //   const result = await sendCampaign(payload);
  //   if (result.success) {
  //     alert(
  //       `Email sent successfully!\nSuccessful: ${result.data.emailResults?.successful || 0}\nFailed: ${result.data.emailResults?.failed || 0}`
  //     );
  //     setFormData({
  //       ...formData,
  //       title: "",
  //       subject: "",
  //       body: "",
  //       department: [],
  //       batch_year: "",
  //       min_cgpa: "",
  //       recipient_emails: "",
  //     });
  //     setSelectedTemplate(null);
  //   } else {
  //     alert("Failed to send email: " + result.message);
  //   }
  // };

  // Get attachment count from selected template

  const handleSend = async () => {
    if (!formData.title || !formData.subject || !formData.body) {
      alert("Please fill title, subject, and body");
      return;
    }

    if (!confirm("Are you sure you want to send this email?")) return;

    // Create FormData for file uploads
    const formDataToSend = new FormData();
    formDataToSend.append("title", formData.title);
    formDataToSend.append("subject", formData.subject);
    formDataToSend.append("body", formData.body);
    formDataToSend.append("sender_email", formData.sender_email);

    // Add template_id if selected
    if (selectedTemplate) {
      formDataToSend.append("template_id", selectedTemplate.id);
    }

    // Add manual attachments
    manualAttachments.forEach((file) => {
      formDataToSend.append("manual_attachments", file);
    });

    if (formData.sendType === "filter") {
      const filters = {};
      if (formData.department.length > 0)
        filters.department = formData.department;
      if (formData.batch_year)
        filters.batch_year = parseInt(formData.batch_year);
      if (formData.min_cgpa) filters.min_cgpa = parseFloat(formData.min_cgpa);
      if (formData.placement_status)
        filters.placement_status = formData.placement_status;
      formDataToSend.append("recipient_filter", JSON.stringify(filters));
    } else if (formData.sendType === "manual") {
      const emails = formData.recipient_emails
        .split(",")
        .map((e) => e.trim())
        .filter((e) => e);
      if (emails.length === 0) {
        alert("Please enter at least one email address");
        return;
      }
      formDataToSend.append("recipient_emails", JSON.stringify(emails));
    }

    const result = await sendCampaign(formDataToSend);
    if (result.success) {
      alert(
        `Email sent successfully!\nSuccessful: ${result.data.emailResults?.successful || 0}\nFailed: ${result.data.emailResults?.failed || 0}`
      );
      setFormData({
        ...formData,
        title: "",
        subject: "",
        body: "",
        department: [],
        batch_year: "",
        min_cgpa: "",
        recipient_emails: "",
      });
      setSelectedTemplate(null);
      setManualAttachments([]);
    } else {
      alert("Failed to send email: " + result.message);
    }
  };
  const getAttachmentCount = () => {
    if (!selectedTemplate?.attachments) return 0;
    try {
      const attachments =
        typeof selectedTemplate.attachments === "string"
          ? JSON.parse(selectedTemplate.attachments)
          : selectedTemplate.attachments;
      return attachments.length;
    } catch (err) {
      return 0;
    }
  };

  return (
    <div className="space-y-6">
      {/* Quick Templates */}
      {templates.length > 0 && (
        <div className="bg-gradient-to-r from-blue-50 to-indigo-50 rounded-xl p-6 border border-blue-100">
          <div className="flex items-center gap-2 mb-4">
            <FileText className="w-5 h-5 text-blue-600" />
            <h3 className="font-semibold text-gray-900">
              Quick Start Templates
            </h3>
          </div>
          <div className="flex flex-wrap gap-2">
            {templates.map((template) => {
              const isSelected = selectedTemplate?.id === template.id;
              let attachmentCount = 0;
              try {
                if (template.attachments) {
                  const attachments =
                    typeof template.attachments === "string"
                      ? JSON.parse(template.attachments)
                      : template.attachments;
                  attachmentCount = attachments.length;
                }
              } catch (err) {
                console.error("Error parsing attachments:", err);
              }

              return (
                <button
                  key={template.id}
                  onClick={() => handleUseTemplate(template)}
                  className={`px-4 py-2 border rounded-lg text-sm font-medium transition-all flex items-center gap-2 ${
                    isSelected
                      ? "bg-blue-600 text-white border-blue-600"
                      : "bg-white border-blue-200 text-blue-700 hover:bg-blue-50 hover:border-blue-300"
                  }`}
                >
                  {template.template_name}
                  {attachmentCount > 0 && (
                    <span
                      className={`inline-flex items-center gap-1 text-xs ${
                        isSelected ? "text-blue-100" : "text-blue-600"
                      }`}
                    >
                      <Paperclip className="w-3 h-3" />
                      {attachmentCount}
                    </span>
                  )}
                </button>
              );
            })}
          </div>
        </div>
      )}

      <div className="bg-white rounded-xl border border-gray-200 p-6 space-y-6">
        {/* Selected Template Info with Attachments */}
        {selectedTemplate && (
          <div className="bg-green-50 border border-green-200 rounded-lg p-4">
            <div className="flex items-start justify-between">
              <div className="flex-1">
                <div className="flex items-center gap-2 mb-2">
                  <FileText className="w-5 h-5 text-green-600" />
                  <h4 className="font-semibold text-green-900">
                    Using Template: {selectedTemplate.template_name}
                  </h4>
                </div>
                {getAttachmentCount() > 0 && (
                  <div className="flex items-center gap-2 text-sm text-green-700">
                    <Paperclip className="w-4 h-4" />
                    <span>
                      {getAttachmentCount()} attachment
                      {getAttachmentCount() !== 1 ? "s" : ""} will be included
                      with this email
                    </span>
                  </div>
                )}
              </div>
              <button
                onClick={() => {
                  setSelectedTemplate(null);
                  setFormData({
                    ...formData,
                    title: "",
                    subject: "",
                    body: "",
                  });
                }}
                className="p-1 hover:bg-green-100 rounded transition-colors"
              >
                <X className="w-4 h-4 text-green-600" />
              </button>
            </div>
          </div>
        )}

        {/* Basic Info */}
        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
          <div>
            <label className="block text-sm font-semibold text-gray-700 mb-2">
              Campaign Title
            </label>
            <input
              type="text"
              value={formData.title}
              onChange={(e) =>
                setFormData({ ...formData, title: e.target.value })
              }
              className="w-full border border-gray-300 rounded-lg px-4 py-2.5 focus:ring-2 focus:ring-blue-500 focus:border-transparent"
              placeholder="e.g., Google Drive Reminder - Jan 2024"
            />
          </div>
          <div>
            <label className="block text-sm font-semibold text-gray-700 mb-2">
              Sender Email
            </label>
            <input
              type="email"
              value={formData.sender_email}
              onChange={(e) =>
                setFormData({ ...formData, sender_email: e.target.value })
              }
              className="w-full border border-gray-300 rounded-lg px-4 py-2.5 focus:ring-2 focus:ring-blue-500 focus:border-transparent"
            />
          </div>
        </div>

        {/* Subject */}
        <div>
          <label className="block text-sm font-semibold text-gray-700 mb-2">
            Email Subject
          </label>
          <input
            type="text"
            value={formData.subject}
            onChange={(e) =>
              setFormData({ ...formData, subject: e.target.value })
            }
            className="w-full border border-gray-300 rounded-lg px-4 py-2.5 focus:ring-2 focus:ring-blue-500 focus:border-transparent"
            placeholder="Use {first_name}, {last_name}, {cgpa}, etc."
          />
        </div>

        {/* Body */}
        <div>
          <label className="block text-sm font-semibold text-gray-700 mb-2">
            Email Body (HTML)
          </label>
          <textarea
            value={formData.body}
            onChange={(e) => setFormData({ ...formData, body: e.target.value })}
            className="w-full border border-gray-300 rounded-lg px-4 py-2.5 h-48 font-mono text-sm focus:ring-2 focus:ring-blue-500 focus:border-transparent"
            placeholder="<p>Dear {first_name},</p><p>Your CGPA is {cgpa}...</p>"
          />
          <p className="text-xs text-gray-500 mt-2">
            Available variables: {"{first_name}"}, {"{last_name}"}, {"{cgpa}"},{" "}
            {"{department}"}, {"{enrollment_number}"}, {"{batch_year}"}
          </p>
        </div>
        {/* Manual Attachments Section */}
        <div>
          <label className="block text-sm font-semibold text-gray-700 mb-2">
            Additional Attachments
          </label>
          <div className="space-y-3">
            <div className="flex items-center gap-3">
              <button
                type="button"
                onClick={() => fileInputRef.current?.click()}
                disabled={getTotalAttachmentCount() >= 5}
                className="px-4 py-2 bg-gray-100 hover:bg-gray-200 disabled:bg-gray-50 disabled:text-gray-400 text-gray-700 rounded-lg flex items-center gap-2"
              >
                <Paperclip className="w-4 h-4" />
                Add Files
              </button>
              <span className="text-sm text-gray-500">
                Max 5 files total ({getTotalAttachmentCount()}/5 used)
              </span>
            </div>
            <input
              type="file"
              ref={fileInputRef}
              onChange={handleFileSelect}
              multiple
              className="hidden"
              accept=".pdf,.doc,.docx,.xls,.xlsx,.jpg,.jpeg,.png"
            />

            {/* Manual Attachments List */}
            {manualAttachments.length > 0 && (
              <div className="mt-3 p-4 bg-purple-50 rounded-lg border border-purple-200">
                <p className="text-xs font-semibold text-purple-900 mb-2">
                  Manual Attachments ({manualAttachments.length})
                </p>
                <div className="space-y-2">
                  {manualAttachments.map((file, index) => (
                    <div
                      key={index}
                      className="flex items-center justify-between bg-white p-2 rounded"
                    >
                      <div className="flex items-center gap-2 flex-1">
                        <Paperclip className="w-4 h-4 text-purple-500" />
                        <span className="text-sm text-gray-700 truncate">
                          {file.name}
                        </span>
                        <span className="text-xs text-gray-500">
                          ({(file.size / 1024 / 1024).toFixed(2)} MB)
                        </span>
                      </div>
                      <button
                        onClick={() => removeManualAttachment(index)}
                        className="p-1 hover:bg-red-100 rounded-full"
                      >
                        <X className="w-4 h-4 text-red-500" />
                      </button>
                    </div>
                  ))}
                </div>
              </div>
            )}
          </div>
        </div>

        {/* Recipients */}
        <div>
          <label className="block text-sm font-semibold text-gray-700 mb-3">
            Send To
          </label>
          <div className="flex flex-wrap gap-4 mb-4">
            {["filter", "manual"].map((type) => (
              <label key={type} className="flex items-center cursor-pointer">
                <input
                  type="radio"
                  value={type}
                  checked={formData.sendType === type}
                  onChange={(e) =>
                    setFormData({ ...formData, sendType: e.target.value })
                  }
                  className="w-4 h-4 text-blue-600 focus:ring-2 focus:ring-blue-500"
                />
                <span className="ml-2 text-sm font-medium text-gray-700 capitalize">
                  {type === "filter" ? "Filter Students" : "Manual Emails"}
                </span>
              </label>
            ))}
          </div>

          {formData.sendType === "filter" && (
            <div className="bg-gray-50 rounded-lg p-4 space-y-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Department (comma-separated)
                </label>
                <input
                  type="text"
                  value={formData.department.join(", ")}
                  onChange={(e) =>
                    setFormData({
                      ...formData,
                      department: e.target.value
                        .split(",")
                        .map((d) => d.trim()),
                    })
                  }
                  className="w-full border border-gray-300 rounded-lg px-4 py-2"
                  placeholder="CSE, ECE, ME"
                />
              </div>
              <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Batch Year
                  </label>
                  <input
                    type="number"
                    value={formData.batch_year}
                    onChange={(e) =>
                      setFormData({ ...formData, batch_year: e.target.value })
                    }
                    className="w-full border border-gray-300 rounded-lg px-4 py-2"
                    placeholder="2024"
                  />
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Min CGPA
                  </label>
                  <input
                    type="number"
                    step="0.1"
                    value={formData.min_cgpa}
                    onChange={(e) =>
                      setFormData({ ...formData, min_cgpa: e.target.value })
                    }
                    className="w-full border border-gray-300 rounded-lg px-4 py-2"
                    placeholder="7.0"
                  />
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Placement Status
                  </label>
                  <select
                    value={formData.placement_status}
                    onChange={(e) =>
                      setFormData({
                        ...formData,
                        placement_status: e.target.value,
                      })
                    }
                    className="w-full border border-gray-300 rounded-lg px-4 py-2"
                  >
                    <option value="">All</option>
                    <option value="eligible">Eligible</option>
                    <option value="placed">Placed</option>
                    <option value="unplaced">Unplaced</option>
                  </select>
                </div>
              </div>
            </div>
          )}

          {formData.sendType === "manual" && (
            <div className="bg-gray-50 rounded-lg p-4">
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Email Addresses (comma-separated)
              </label>
              <textarea
                value={formData.recipient_emails}
                onChange={(e) =>
                  setFormData({ ...formData, recipient_emails: e.target.value })
                }
                className="w-full border border-gray-300 rounded-lg px-4 py-2 h-24"
                placeholder="student1@college.edu, student2@college.edu"
              />
            </div>
          )}
        </div>

        {/* Actions */}
        <div className="flex gap-3 pt-4 border-t">
          <button
            onClick={handlePreview}
            disabled={loading}
            className="px-6 py-2.5 bg-gray-600 text-white rounded-lg hover:bg-gray-700 disabled:opacity-50 flex items-center gap-2 font-medium transition-all"
          >
            <Eye className="w-4 h-4" />
            Preview
          </button>
          <button
            onClick={handleSend}
            disabled={loading}
            className="px-6 py-2.5 bg-gradient-to-r from-blue-600 to-indigo-600 text-white rounded-lg hover:from-blue-700 hover:to-indigo-700 disabled:opacity-50 flex items-center gap-2 font-medium transition-all shadow-lg"
          >
            <Send className="w-4 h-4" />
            {loading ? "Sending..." : "Send Email"}
          </button>
        </div>
      </div>

      {/* Preview Modal */}
      {previewData && (
        <div className="fixed inset-0 bg-black/60 backdrop-blur-sm flex items-center justify-center p-4 z-50">
          <div className="bg-white rounded-2xl max-w-3xl w-full max-h-[85vh] overflow-hidden shadow-2xl">
            <div className="flex justify-between items-center p-6 border-b">
              <h3 className="text-xl font-bold text-gray-900">Email Preview</h3>
              <button
                onClick={() => setPreviewData(null)}
                className="p-2 hover:bg-gray-100 rounded-lg transition-colors"
              >
                <X className="w-5 h-5" />
              </button>
            </div>
            <div className="p-6 overflow-y-auto max-h-[calc(85vh-80px)] space-y-4">
              <div>
                <p className="text-sm font-semibold text-gray-600 mb-1">
                  Subject:
                </p>
                <p className="text-lg font-medium">{previewData.subject}</p>
              </div>
              <div>
                <p className="text-sm font-semibold text-gray-600 mb-2">
                  Body:
                </p>
                <div
                  className="border rounded-lg p-4 bg-gray-50"
                  dangerouslySetInnerHTML={{ __html: previewData.body }}
                />
              </div>

              {/* Show attachments in preview */}
              {previewData.attachments &&
                previewData.attachments.length > 0 && (
                  <div>
                    <p className="text-sm font-semibold text-gray-600 mb-2">
                      Attachments:
                    </p>
                    <div className="border rounded-lg p-4 bg-gray-50 space-y-2">
                      {previewData.attachments.map((attachment, index) => (
                        <div
                          key={index}
                          className="flex items-center gap-2 text-sm text-gray-700"
                        >
                          <Paperclip className="w-4 h-4 text-blue-500" />
                          <span>{attachment.name}</span>
                          <span className="text-xs text-gray-500">
                            ({attachment.type})
                          </span>
                        </div>
                      ))}
                    </div>
                  </div>
                )}
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default ComposeEmail;
