// ===== Templates.js (Frontend) =====
import { useState, useRef } from "react";
import { Mail, Trash2, Save, Edit, Paperclip, X } from "lucide-react";
import { useEmail } from "../../context/EmailContext";

const Templates = () => {
  const { templates, createTemplate, updateTemplate, deleteTemplate, loading } =
    useEmail();
  const fileInputRef = useRef(null);
  const [form, setForm] = useState({
    template_name: "",
    subject: "",
    body: "",
    category: "general",
  });
  const [editing, setEditing] = useState(null);
  const [selectedFiles, setSelectedFiles] = useState([]);
  const [existingAttachments, setExistingAttachments] = useState([]);
  const [removedAttachments, setRemovedAttachments] = useState([]);

  const handleFileSelect = (e) => {
    const files = Array.from(e.target.files);
    setSelectedFiles((prev) => [...prev, ...files]);
  };

  const removeNewFile = (index) => {
    setSelectedFiles((files) => files.filter((_, i) => i !== index));
  };

  const removeExistingAttachment = (index) => {
    const removed = existingAttachments[index];
    setRemovedAttachments((prev) => [...prev, removed.filename]);
    setExistingAttachments((files) => files.filter((_, i) => i !== index));
  };

  const handleSave = async () => {
    if (!form.template_name || !form.subject || !form.body) {
      alert("Please fill all fields");
      return;
    }

    const formData = new FormData();
    formData.append("template_name", form.template_name);
    formData.append("subject", form.subject);
    formData.append("body", form.body);
    formData.append("category", form.category);

    selectedFiles.forEach((file) => {
      formData.append("attachments", file);
    });

    if (editing) {
      if (existingAttachments.length > 0) {
        formData.append("keep_existing_attachments", "true");
      }
      if (removedAttachments.length > 0) {
        formData.append(
          "removed_attachments",
          JSON.stringify(removedAttachments)
        );
      }
    }

    const result = editing
      ? await updateTemplate(editing.id, formData)
      : await createTemplate(formData);

    if (result.success) {
      alert(result.message);
      resetForm();
    } else {
      alert("Failed to save: " + result.message);
    }
  };

  const resetForm = () => {
    setForm({
      template_name: "",
      subject: "",
      body: "",
      category: "general",
    });
    setSelectedFiles([]);
    setExistingAttachments([]);
    setRemovedAttachments([]);
    setEditing(null);
  };

  const handleDelete = async (id) => {
    if (!confirm("Delete this template?")) return;
    const result = await deleteTemplate(id);
    if (result.success) {
      alert("Template deleted");
    }
  };

  const handleEdit = (template) => {
    setEditing(template);
    setForm({
      template_name: template.template_name,
      subject: template.subject,
      body: template.body,
      category: template.category,
    });

    // Always reset attachment states first
    setExistingAttachments([]);
    setSelectedFiles([]);
    setRemovedAttachments([]);

    // Then set attachments if they exist
    if (template.attachments) {
      try {
        // Check if it's already an object/array or a string
        const attachments =
          typeof template.attachments === "string"
            ? JSON.parse(template.attachments)
            : template.attachments;
        setExistingAttachments(attachments);
      } catch (err) {
        console.error("Error parsing attachments:", err);
      }
    }
  };

  return (
    <div className="space-y-6">
      {/* Form */}
      <div className="bg-white rounded-xl border border-gray-200 p-6">
        <h3 className="text-lg font-bold text-gray-900 mb-4">
          {editing ? "Edit Template" : "Create New Template"}
        </h3>
        <div className="space-y-4">
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div>
              <label className="block text-sm font-semibold text-gray-700 mb-2">
                Template Name
              </label>
              <input
                type="text"
                value={form.template_name}
                onChange={(e) =>
                  setForm({ ...form, template_name: e.target.value })
                }
                className="w-full border border-gray-300 rounded-lg px-4 py-2.5 focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                placeholder="Welcome Email"
              />
            </div>
            <div>
              <label className="block text-sm font-semibold text-gray-700 mb-2">
                Category
              </label>
              <select
                value={form.category}
                onChange={(e) => setForm({ ...form, category: e.target.value })}
                className="w-full border border-gray-300 rounded-lg px-4 py-2.5 focus:ring-2 focus:ring-blue-500 focus:border-transparent"
              >
                <option value="general">General</option>
                <option value="placement">Placement</option>
                <option value="event">Event</option>
                <option value="reminder">Reminder</option>
              </select>
            </div>
          </div>
          <div>
            <label className="block text-sm font-semibold text-gray-700 mb-2">
              Subject
            </label>
            <input
              type="text"
              value={form.subject}
              onChange={(e) => setForm({ ...form, subject: e.target.value })}
              className="w-full border border-gray-300 rounded-lg px-4 py-2.5 focus:ring-2 focus:ring-blue-500 focus:border-transparent"
              placeholder="Welcome to our platform"
            />
          </div>
          <div>
            <label className="block text-sm font-semibold text-gray-700 mb-2">
              Body (HTML)
            </label>
            <textarea
              value={form.body}
              onChange={(e) => setForm({ ...form, body: e.target.value })}
              className="w-full border border-gray-300 rounded-lg px-4 py-2.5 h-32 font-mono text-sm focus:ring-2 focus:ring-blue-500 focus:border-transparent"
              placeholder="Hello {first_name}..."
            />
          </div>

          {/* File Attachments Section */}
          <div>
            <label className="block text-sm font-semibold text-gray-700 mb-2">
              Attachments
            </label>
            <div className="space-y-2">
              <div className="flex items-center gap-2">
                <button
                  type="button"
                  onClick={() => fileInputRef.current?.click()}
                  className="px-4 py-2 bg-gray-100 hover:bg-gray-200 text-gray-700 rounded-lg flex items-center gap-2 transition-colors"
                >
                  <Paperclip className="w-4 h-4" />
                  Add Files
                </button>
                <span className="text-sm text-gray-500">
                  Max 5 files, 10MB each
                </span>
              </div>
              <input
                type="file"
                ref={fileInputRef}
                onChange={handleFileSelect}
                multiple
                className="hidden"
                accept=".pdf,.doc,.docx,.xls,.xlsx,.jpg,.jpeg,.png"
              />

              {/* Existing Attachments (when editing) */}
              {existingAttachments.length > 0 && (
                <div className="mt-4 p-3 bg-blue-50 rounded-lg border border-blue-200">
                  <p className="text-xs font-semibold text-blue-900 mb-2">
                    Existing Attachments
                  </p>
                  <div className="space-y-2">
                    {existingAttachments.map((file, index) => (
                      <div
                        key={index}
                        className="flex items-center justify-between bg-white p-2 rounded"
                      >
                        <div className="flex items-center gap-2">
                          <Paperclip className="w-4 h-4 text-blue-500" />
                          <span className="text-sm text-gray-700">
                            {file.filename}
                          </span>
                        </div>
                        <button
                          onClick={() => removeExistingAttachment(index)}
                          className="p-1 hover:bg-red-100 rounded-full transition-colors"
                        >
                          <X className="w-4 h-4 text-red-500" />
                        </button>
                      </div>
                    ))}
                  </div>
                </div>
              )}

              {/* New Selected Files */}
              {selectedFiles.length > 0 && (
                <div className="mt-3 p-3 bg-green-50 rounded-lg border border-green-200">
                  <p className="text-xs font-semibold text-green-900 mb-2">
                    New Files to Upload
                  </p>
                  <div className="space-y-2">
                    {selectedFiles.map((file, index) => (
                      <div
                        key={index}
                        className="flex items-center justify-between bg-white p-2 rounded"
                      >
                        <div className="flex items-center gap-2">
                          <Paperclip className="w-4 h-4 text-green-500" />
                          <span className="text-sm text-gray-700">
                            {file.name}
                          </span>
                          <span className="text-xs text-gray-500">
                            ({(file.size / 1024 / 1024).toFixed(2)} MB)
                          </span>
                        </div>
                        <button
                          onClick={() => removeNewFile(index)}
                          className="p-1 hover:bg-red-100 rounded-full transition-colors"
                        >
                          <X className="w-4 h-4 text-red-500" />
                        </button>
                      </div>
                    ))}
                  </div>
                </div>
              )}
            </div>
          </div>

          <div className="flex gap-3">
            <button
              onClick={handleSave}
              disabled={loading}
              className="px-6 py-2.5 bg-gradient-to-r from-blue-600 to-indigo-600 text-white rounded-lg hover:from-blue-700 hover:to-indigo-700 disabled:opacity-50 flex items-center gap-2 font-medium transition-all shadow-lg"
            >
              <Save className="w-4 h-4" />
              {editing ? "Update" : "Save"} Template
            </button>
            {editing && (
              <button
                onClick={resetForm}
                className="px-6 py-2.5 bg-gray-500 text-white rounded-lg hover:bg-gray-600 font-medium transition-all"
              >
                Cancel
              </button>
            )}
          </div>
        </div>
      </div>

      {/* Templates List */}
      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
        {templates.map((template) => (
          <div
            key={template.id}
            className="bg-white rounded-xl border border-gray-200 p-5 hover:shadow-md transition-all"
          >
            <div className="flex justify-between items-start mb-3">
              <div className="flex-1">
                <h4 className="font-bold text-gray-900 mb-1">
                  {template.template_name}
                </h4>
                <p className="text-sm text-gray-600 mb-2">{template.subject}</p>
                <div className="flex items-center gap-2">
                  <span className="inline-block px-3 py-1 bg-blue-100 text-blue-700 text-xs font-medium rounded-full">
                    {template.category}
                  </span>
                  {template.attachments && (
                    <span className="inline-flex items-center gap-1 px-3 py-1 bg-gray-100 text-gray-700 text-xs font-medium rounded-full">
                      <Paperclip className="w-3 h-3" />
                      {/* Safely parse attachments */}
                      {(() => {
                        try {
                          const attachments =
                            typeof template.attachments === "string"
                              ? JSON.parse(template.attachments)
                              : template.attachments;
                          return `${attachments.length} files`;
                        } catch (err) {
                          console.error("Error parsing attachments:", err);
                          return "0 files";
                        }
                      })()}
                    </span>
                  )}
                </div>
              </div>
              <div className="flex gap-2">
                <button
                  onClick={() => handleEdit(template)}
                  className="p-2 text-green-600 hover:bg-green-50 rounded-lg transition-colors"
                  title="Edit"
                >
                  <Edit className="w-4 h-4" />
                </button>
                <button
                  onClick={() => handleDelete(template.id)}
                  className="p-2 text-red-600 hover:bg-red-50 rounded-lg transition-colors"
                  title="Delete"
                >
                  <Trash2 className="w-4 h-4" />
                </button>
              </div>
            </div>
          </div>
        ))}
        {templates.length === 0 && (
          <div className="col-span-2 text-center py-12 text-gray-500">
            <Mail className="w-12 h-12 mx-auto mb-3 opacity-30" />
            <p>No templates yet. Create one above!</p>
          </div>
        )}
      </div>
    </div>
  );
};

export default Templates;
